@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500&display=swap);
:root {
  --primary-color:#A9BEEB;
  --secondary-color:#F3F399;
  --black-color: #000000;
  --white-color: #ffffff;
  --blue-light-color: #3a80b8;
  --blue-light-color1: #E1E8FC;
}
html {
  scroll-behavior: smooth;
}
body, html{
  font-family: 'Poppins', sans-serif;
}
body {
  overflow-x: hidden;
}
h1, .h1 {font-size: 60px;}
h2, .h2 {font-size: 34px; font-weight: 800;}
h6, .h6 {font-size: 22px; font-weight: 600;}
p {font-size: 16px;}
p:last-child {
  margin-bottom: 0;
}
.btn-blue {
  background: #3a80b8;
  background: var(--blue-light-color);
}

.bn-onboard-modal {
  z-index: 100 !important;
}

@media (min-width: 1400px) {
  .container {
    max-width: 1100px;
  }
}
/* section   */
.common-section {
  padding-top: 20px;
}

@media (max-width:767px){
  h1, .h1 {font-size: 50px;}
  h2, .h2 {font-size: 30px;}
  h6, .h6 {font-size: 20px;}
}
 
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.footer {padding:20px;background:#fcfcfc;}
.f_sec img {width: 260px;}
.f_sec p {font-size: 14px; margin: 10px 0 0; }
.footer .social_icons li:last-child {margin-right:0;}
/* navbar start */
.nav-bar-container {
    padding:30px 0;
    background: #a9beeb;
    position: fixed;
    top: 0;
    width: 100%; 
    z-index: 111;
  }
  .mobile-nav-bar{
    display: none;
  }
  .nav-bar{
    display: flex;
    justify-content: center;
    text-align: center; 
  }
  .nav-bar .active span{
    border-bottom: 4px solid var(--black-color);
  }
  .nav-bar div span {
    margin: 0 35px;
  }
  .nav-bar div span a {
    font-size: 22px;
    font-weight:800;
    color: #000;
    padding:0;
    text-decoration: none;
    border-bottom: 2px solid transparent;
  }
  .btn-wallet {
    background-color: #ee1810;
    color:  var(--white-color);
    display: inline-block;
    position: fixed;
    right: 40px;
    padding: 10px 15px;
    border-radius: 0;
    font-weight: 700;
    font-size: 16px;
    border: 0;
    height: 54px; 
    min-width: 150px;
}
.btn-wallet:hover {
    background: #e37558;
    color: var(--white-color);
}

  
 /* Media Css */

@media screen and (max-width: 1200px) {
  .nav-bar-container {padding:20px 0;}
  .nav-bar div span {
    margin: 0 25px;
  }
  .nav-bar div span a {font-size: 18px;}
}

@media screen and (max-width: 996px) {
    .nav-bar-container {
        padding:10px 0;
    }
    .desktop-nav-bar{
      display: none;
    }
    .nav-bar.mob-nav-bar>div {
      width: 100%;
    }
    .nav-bar.mob-nav-bar {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      background-color: var(--white-color);
      max-height: calc(100% - 60px);
      overflow-x: hidden;
      overflow-y: auto;
      position: fixed;
      z-index: 11;
      top: 60px;
      left: 0;
      width: 100%;
      box-shadow: -8px 9px 18px rgba(0,0,0,0.2);
    } 
    .nav-bar div span {
      margin: 0;
      display: block;
    }
    .nav-bar div span a {
      padding:20px 0; 
      display: block;
      line-height: 40px;
      border-bottom: 1px solid var(--blue-light-color);
    }
    .mobile-nav-bar {
      font-size: 22px;
      font-weight: 700;
      padding:0 15px;
      text-align: left;
    }
    .mobile-nav-bar svg{
     font-size: 40px;
     float: right;
    }
    .mobile-nav-bar {
      display:  block;
      line-height: 40px;
    }
    
}

@media screen and (min-width: 997px) {
  .mob-nav-bar {
    display: none;
  }
}

/* start about-section */
.about-section{
    background-color: #F4F4A1;
}
/* About sec 2 */
.about_sec_2 {background-color: var(--blue-light-color1);}
.about_sec_2 h2 {margin: 0 0 20px;}
.about-cat-content {padding:50px;}
.about-cat-content p {margin-bottom: 20px; color: var(--black-color);}

@media (max-width:767px){
    .about-cat-content {padding:30px;}
}
.accordion-item {
    background: transparent;
    margin: 30px 0 0;
    border:0;
}
.accordion-item .accordion-button {
    color: var(--black-color);
    background: transparent;
    font-weight: 700;
    box-shadow: none;
    font-size: 20px;
    padding:10px 0;
    border: 0;
}
.accordion-body {
    padding:0 20px;
}

.accordion-button:not(.collapsed)::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 16 16%27 fill=%27%23212529%27%3e%3cpath fill-rule=%27evenodd%27 d=%27M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z%27/%3e%3c/svg%3e") !important;
}

a.ct_ext_link {
    color: #000;
}

@media (max-width:767px) {
    .accordion-item {
        margin:20px 0 0;
    }
    .accordion-body {
        padding:0;
    }
}

/* start FAQ */
.faq-section {
   background: #ffffe6;
   padding:50px 0;
}
.faq-section .accordion-container {
  width: 100%; 
}
.member-section {
    padding:50px 0;
}
.mem_title p {
    text-align: left;  
    color: var(--black-color);
}
.member_col {
    padding:30px;
    text-align: center;
}
.member_col em {
    display: inline-block;
    max-width: 160px;
    margin: 0 0 20px;
}
.member_col h6 {
    color: var(--black-color);
    line-height: 1;
    margin: 0 0 15px;
}
.member_col p {
    text-align: center;
    font-size: 16px;
}
.top_header {
    background:var(--primary-color);
    position: relative;
    padding-top: 150px;
    text-align: center;
}
.top_header:after {
    content: '';
    background: url(/static/media/top_divider.d286325f.jpg) no-repeat;
    background-position: center;
    background-size: 100%;
    position: absolute;
    bottom: -60px;
    left: 0;
    width: 100%;
    padding-top:230px; 
}
.top_header figure {
    position: relative;
    margin: 0;
    z-index:1;
}
.top_header figure img {
    max-width: 70%;
}

/* mint section */
.mint-section { 
    background: rgb(244,243,159);
    background: linear-gradient(360deg, rgba(244,243,159,1) 0%, rgba(255,255,230,1) 70%);
    position: relative;
    padding:0 0 40px;
}

.social_icons {
    margin: 0 auto;
    text-align: center;
}
.social_icons li {display: inline-block; margin: 0 10px;}
.social_icons li a img {width: 44px;}

/*  */
.sec_title h2 { 
    margin: 0 0 10px;
    color: var(--black-color);
}
.sec_title h2 em {
    margin-right: 10px;
}
.sec_title h2 em img {
    width: 45px;
}
.sec_title span {
    font-size: 22px;
    color: var(--blue-light-color);
}

.input_label {
    margin: 20px 20px 20px 0;
}
.input_label button {
    background: var(--black-color);
    color: var(--white-color);
    display: inline-block;
    text-align: center;
    line-height: 36px;
    font-weight: 800;
    font-size: 20px;
    width: 36px;
    height: 36px;
    border: 0;
}
.input_label input {
    width: 180px;
    padding:6px 10px;
    border-radius: 0;
    margin: 0 15px;
    background: var(--white-color);
    border: 1px solid var(--black-color);
    color: var(--black-color);
    font-size: 14px;
    height: 54px;
}
.logo-content {
   padding-left: 20px;
}
.input_group p {
    color: var(--black-color);
    font-size: 16px;
}
.btn-blue{
    background-color: var(--blue-light-color);
    color:  var(--white-color);
    display: inline-block;
    padding: 10px 15px;
    border-radius: 0;
    font-weight: 700;
    font-size: 16px;
    border: 0;
    height: 54px; 
    min-width: 150px;
}
.btn-blue:hover {
    background: var(--primary-color);
    color: var(--white-color);
}

@media (max-width:1200px){
    .top_header {
        padding-top: 100px;
    }
    .top_header:after {
        padding-top:150px;
    }
}

@media (max-width:767px){    
    .top_header {
        padding-top: 60px;
    }
    .top_header:after {
        padding-top:120px;
    }
    .text_data {
        margin: 0 0 15px;
    } 
    .btn-blue {
        margin-left: 0;
    }
}

@media (min-width:1280px) {
    .mint-section .social_icons {
        margin-top: 40px!important;
        margin-bottom: 70px!important;
    }
}

/* start roadMap-section */
.roadMap-section {
    padding:50px 0 30px;
    background-color: var(--secondary-color);
  }
.roadMap-section p { font-weight: 600; text-align: center;}
.center_col {width: 100%;}
  
.left-container {
  height: 100%;
  display: flex;
  text-align: right;
  justify-content: space-between;
}
.roadMap-section .col-heading {
  font-weight: 700;
  font-size: 40px;
}
.right-content {
  padding-left: 100px;
    text-align: right;
}
.roadMap-section .left-content {
  padding-right: 100px;
  text-align: justify;
}
.top-content, .bottom-content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.left-container .top-content {   
  justify-content: center;
}
.left-container .bottom-content {
  justify-content: flex-start;
}
.right-container {
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.right-container .top-content  {
  align-items: flex-start;
  justify-content: flex-end;
}
.right-container .bottom-content {     
  align-items: flex-start;
  justify-content: center;
}

/* Bottom box */
.bottom-text-container {
  background: var(--white-color);
  padding: 25px 60px 25px 25px;
  width: 66%;
  margin: 10px auto 0;
  position: relative;
}
.bottom-text-container h2 {
  font-weight: 700;
  text-transform: uppercase;
}
.bottom-text-container p {
  font-weight: 600;
}
.future-cat {
  position: absolute;
  bottom: 0px;
  right: -79px; 
  margin: 0; 
}
.future-cat img {width: 160px;}

/* Media */
@media screen and (min-width: 992px) {
  .left-container .top-content, .left-container .bottom-content  {
    position: relative;
    right: -10px;
  }
  .right-container .top-content, .right-container .bottom-content  {
    position: relative;
    left: -10px;
  }
}
@media screen and (max-width: 776px) {
  .left-container .top-content {align-items: center;}
  .roadMap-section .right-content {text-align: start; padding: unset;}
  .left-container .bottom-content {align-items: center;}
  .right-container .top-content {align-items: center;}
  .left-content{text-align: center; padding: 0; }
  .right-container .bottom-content{align-items: center;}
  .bottom-text-container {width:96%; right: -10px; margin: 20px 0 0;}  
  .future-cat img {width: 90px;}
}

@media screen and (max-width: 767px) {
  .mob_none {display: none;}
  .roadMap-section .left-content {padding-right:0;}
  .top-content, .bottom-content {padding:10px 0;}
  .roadMap-section {overflow-x: hidden;}
  .roadMap-section .left-content, .roadMap-section .right-content {text-align: center;}
  .future-cat {right: -10px;}
  .bottom-text-container p {text-align: left;}
}
.team-section {
    padding:50px 0;
}
.team_col {
    padding:30px;
    text-align: center;
}
.team_col em {
    display: inline-block;
    max-width: 160px;
    margin: 0 0 20px;
}
.team_col h6 {
    color: var(--black-color);
    line-height: 1;
    margin: 0 0 5px;
}
.team_col p {
    color: var(--black-color);
    text-align: center;
    font-size: 16px;
}
